// import axios from "axios";

let lsSinyalFtx1InchErc = JSON.parse(localStorage.getItem("mw_sinyal_ftx_1inch_erc"));

const state = {
  sinyal: lsSinyalFtx1InchErc != null ? lsSinyalFtx1InchErc.sinyal : [],
  lastFetch: lsSinyalFtx1InchErc != null ? lsSinyalFtx1InchErc.lastFetch : null,
};

const getters = {
  listSinyalFtx1InchErc(state) {
    return state.sinyal;
  },
  lastFetchSinyalFtx1InchErc(state) {
    return state.lastFetch != null ? new Date(state.lastFetch).toLocaleString("id-ID") : "";
  },
};

const actions = {
  registerSinyalFtx1InchErc(context) {
    let sinyal = [];
    context.rootState.coin.coins_ftx_erc.forEach(item => {
      let askPrice = context.rootGetters.getFtxBookCoinPriceAsk(item.nama_coin);
      let bidPrice = context.rootGetters.getFtxBookCoinPriceBid(item.nama_coin);
      sinyal.push({
        symbol: item.nama_coin,
        pair: askPrice.pair,
        modal_kiri_ftx_1inch_erc: context.rootState.setting.settings.modal_kiri_ftx_1inch_erc || 400,
        modal_kanan_ftx_1inch_erc: context.rootState.setting.settings.modal_kanan_ftx_1inch_erc || 400,
        coin_address: item.coin_address,
        binance_ask_price: askPrice.price,
        ask_xprice: askPrice.xprice !== undefined ? askPrice.xprice : null,
        binance_bid_price: bidPrice.price,
        bid_xprice: bidPrice.xprice !== undefined ? bidPrice.xprice : null,
        inch_usdt: 0,
        selisih_pnl_kiri: 0,
        selisih_pnl_kiri_persen: 0,
        inch_sum: 0,
        inch_price: 0,
        selisih_pnl_kanan: 0,
        selisih_pnl_kanan_persen: 0,
        aktif: item.aktif_1inch != undefined ? item.aktif_1inch : true,
        offset_pnl: item.offset_pnl != undefined ? item.offset_pnl : 0,
        status_wd: false,
        status_depo: false,
        fee_wd: 0,
      });
    });
    context.commit("registerSinyalFtx1InchErc", sinyal);
  },
  updateSinyalFtx1InchErc(context, coin) {
    let sinyal = state.sinyal;
    let index = state.sinyal.findIndex(item => {
      return item.symbol == coin.symbol;
    });
    let symbol = sinyal[index];
    symbol = { ...symbol, ...coin.value };
    sinyal.splice(index, 1, symbol);
    context.commit("registerSinyalFtx1InchErc", sinyal);
  },
  clearSinyalFtx1InchErc(context) {
    let sinyal = state.sinyal.map(item => {
      return {
        ...item,
        binance_ask_price: "wait...",
        binance_bid_price: "wait...",
        inch_usdt: "wait...",
        selisih_pnl_kiri: "wait...",
        selisih_pnl_kiri_persen: "wait...",
        inch_sum: "wait...",
        inch_price: "wait...",
        selisih_pnl_kanan: "wait...",
        selisih_pnl_kanan_persen: "wait...",
      };
    });
    // sinyal.filter(item => {{
    // }});
    context.commit("registerSinyalFtx1InchErc", sinyal);
  },
};

const mutations = {
  registerSinyalFtx1InchErc(state, payload) {
    localStorage.setItem("mw_sinyal_ftx_1inch_erc", JSON.stringify({ sinyal: payload, lastFetch: Date.now() }));
    state.sinyal = payload;
    state.lastFetch = Date.now();
  },
};

export default {
  state,
  actions,
  mutations,
  getters,
};
